import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchEmployees } from "../action/action";
import { Icon } from "@iconify/react";
import { DatePicker } from "antd";
const Cards = () => {
  const dispatch = useDispatch();
  const [searchQuery, setSearchQuery] = useState("");
  const employees = useSelector((state) => state.login.employees);
  console.log(employees);
  useEffect(() => {
    dispatch(fetchEmployees());
  }, [dispatch]);
  const filteredEmployees = employees.filter((employee) =>
    employee.fullName.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleSearchInputChange = (event) => {
    setSearchQuery(event.target.value);
  };

  return (
    <>
      <div className="header">
        <div className="left-corner">
          <i className="user-icon bi bi-person-circle"></i>
          <div>Employees</div>
        </div>
        <div className="tabs">
          <div className="tabs-bar">
            <div className="tabs-nav-container">
              <span className="tabs-tab-prev">
                <span className="tabs-tab-prev-icon">
                  <i className="tabs-tab-prev-icon-target"></i>
                </span>
              </span>
              <span className="tabs-tab-next">
                <span className="tabs-tab-next-icon">
                  <i className="tabs-tab-next-icon-target"></i>
                </span>
              </span>
              <div className="tabs-nav-wrap">
                <div className="tabs-nav-scroll">
                  <div className="tabs-nav">
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <a className="anchor"
                          href="/employees/directory"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          Directory
                        </a>
                      </div>
                    </div>
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <a
                          href="/employees/org-chart"
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          Org Chart
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div>
        <div className="search-input" style={{ marginTop: "120px" }}>
          <Icon icon="uil:search" className="search-icon" />
          <input
            type="text"
            placeholder="Search"
            class="search-field"
            value={searchQuery}
            onChange={handleSearchInputChange}
          />
        </div>

        <div className="card-container">
          {filteredEmployees.map((employee) => (
            <div className="card" key={employee.id}>
              <div className="avatar">
                <img src={employee.featuredImageURL} alt="Profile Picture" />
              </div>
              <div className="details">
                <h3 className="name">{employee.fullName}</h3>
                <p className="position">{employee.designationName}</p>
                <p className="department">{employee.department}</p>
                <div className="contact">
                  <i className="bi bi-telephone"></i>
                  <span>{employee.phoneNumber}</span>
                </div>
                <div className="contact">
                  <i className="fas fa-envelope"></i>
                  <a href={`mailto:${employee.email}`}>{employee.email}</a>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export default Cards;
